<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <Btn class="adv-g__header__filter-btn"
         v-bind:type="type"
         v-on:click="$emit('click', $event)">
        <template v-slot:icon>
            <FilterIcon class="adv-btn__icon"></FilterIcon>
        </template>
        <template v-slot:name>{{i18n.filter.filterBtnLabel}}</template>
    </Btn>
</template>

<script>
    import Btn from "../../ui/Button";
    import FilterIcon from './../../../assets/filter-results-button.svg';
    import {i18nAwareMixin} from "../../../mixins/i18n/I18nAware";

    export default {
        name: "FilterButton",
        components: {Btn, FilterIcon},
        mixins: [i18nAwareMixin],
        props: {
            filterApplied: {
                type: Boolean,
                required: true
            }
        },
        computed: {
            type() {
                return this.filterApplied
                    ? 'primary_outline'
                    : 'secondary_outline';
            }
        }
    }
</script>

<style scoped>
    .adv-g__header__filter-btn {
        height: 64px;
        padding-left: 25px;
        padding-right: 25px;
    }
</style>