<template>
    <modal v-on:close="$emit('close')" class="adv-v__details-modal">
        <template v-slot:title>
            <heading size="h1" v-if="isPromotion">{{i18n.offerInformation.modalTitles.informationAboutPromotion}}</heading>
            <heading size="h1" v-if="isCoupon">{{i18n.offerInformation.modalTitles.informationAboutCoupon}}</heading>
            <heading size="h1" v-if="isPromocode">{{i18n.offerInformation.modalTitles.informationAboutPromocode}}</heading>
        </template>
        <template v-slot:body>
            <promotion-information
                    v-if="isPromotion"
                    v-bind:promotion="offer"
                    v-on:openMap="$emit('openMap')"
            ></promotion-information>
            <coupon-information
                    v-if="isCoupon"
                    v-bind:coupon="offer"
                    v-on:openMap="$emit('openMap')"
            ></coupon-information>
            <promocode-information
                    v-if="isPromocode"
                    v-bind:promocode="offer"
            ></promocode-information>
        </template>
        <template v-slot:footer-left>
            <Btn type="primary" v-on:click="downloadFile">
                <template v-slot:icon>
                    <DownloadSvg
                            v-if="!loading"
                            class="adv-btn__icon"
                    ></DownloadSvg>
                    <Spinner
                            v-if="loading"
                            class="adv-btn__icon"
                    ></Spinner>
                </template>
                <template v-slot:name>
                    <span>{{offer.downloadString}}</span>
                </template>
            </Btn>
        </template>
        <template v-slot:footer-right>
            <Btn
                    type="secondary_outline"
                    v-on:click="$emit('openMap')"
                    v-if="config.isMapActive && (isCoupon || isPromotion)"
            >
                <template v-slot:icon>
                    <MapSvg class="adv-btn__icon"></MapSvg>
                </template>
                <template v-slot:name>
                    <span>{{i18n.offerInformation.mapBtnLabel}}</span>
                </template>
            </Btn>
            <a
                    v-if="isPromocode && offer.promocodeLinks && offer.promocodeLinks.length"
                    v-bind:href="offer.promocodeLinks[0]"
                    target="_blank"
            >
                <Btn
                        type="secondary_outline"
                >
                    <template v-slot:icon>
                        <LinkSvg class="adv-btn__icon"></LinkSvg>
                    </template>
                    <template v-slot:name>
                        <span>{{i18n.offerInformation.linkBtnLabel}}</span>
                    </template>
                </Btn>
            </a>
        </template>
    </modal>
</template>

<script>
    import Modal from "../../ui/Modal";
    import Btn from "../../ui/Button";
    import DownloadSvg from '../../../assets/download.svg';
    import MapSvg from '../../../assets/map.svg';
    import LinkSvg from '../../../assets/link.svg';

    import PromotionInformation from "./PromotionInformation";
    import Heading from "../../ui/Heading";
    import {DownloadFileMixin} from '../../../mixins/DownloadFile';
    import {GalleryConfigAwareMixin} from '../../../mixins/GalleryConfigAware';
    import Spinner from '../../../assets/anitmated-spinner.svg';
    import CouponInformation from "./CouponInformation";
    import PromocodeInformation from "./PromocodeInformation";
    import {i18nAwareMixin} from "../../../mixins/i18n/I18nAware";

    export default {
        name: "details-information",
        components: {
            PromocodeInformation,
            CouponInformation,
            Heading,
            PromotionInformation,
            Btn,
            Modal,
            DownloadSvg,
            Spinner,
            MapSvg,
            LinkSvg
        },
        mixins: [DownloadFileMixin, GalleryConfigAwareMixin, i18nAwareMixin],
        props: {
            offer: Object
        },
        methods: {
            closeModal() {
                this.$emit('close');
            }
        },
        computed: {
            offerType() {
                return this.offer.offerType;
            },
            isPromotion() {
                return this.offerType === 'PROMOTION';
            },
            isCoupon() {
                return this.offerType === 'COUPON';
            },
            isPromocode() {
                return this.offerType === 'PROMOCODE';
            }
        }
    }
</script>

<style scoped>
    .adv-v__details-modal {
        width: 500px;
        height: 600px;
    }
</style>
